import React, { Component } from 'react'




export default class About extends Component {
  render() {
    return (
      <div>
        <section className="colorlib-about" data-section="about">
        <div className="colorlib-narrow-content">
            <div className="row">
            <div className="col-md-12">
                <div className="row row-bottom-padded-sm animate-box" data-animate-effect="fadeInLeft">
                <div className="col-md-12">
                    <div className="about-desc">
                    <span className="urbanist-500">Who Am I?</span>

                    <h2 className="urbanist-italics-500">About Me</h2>
                    <p>I'm a junior undergraduate student currently attending Northeastern University and working towards a Bachelor of Science in Electrical and Computer Engineering. I live and breathe everything-computers: from the interfaces that clients interact with, to high-speed networking, to low-level embedded programming, I love what I do!</p>
                    <p>My unique skill set of both leadership and programming experiences has given me the ability to contribute to meaningful and important projects, such as the development of Terahertz communications at the Ultrabroadband Laboratory in Northeastern University and the maintenance and development of new satellite management software at Kythera Space Solutions.</p>
                    <p>I am always searching for projects to contribute to and will strive to use my existing background experiences to bring a unique perspective to any team.</p>
                    </div>
                </div>
                </div>
            </div>
            </div>
        </div>
        </section>
        <section className="colorlib-about">
        <div className="colorlib-narrow-content">
            <div className="row">
                <div className="col-md-6 col-md-offset-3 col-md-pull-3 animate-box" data-animate-effect="fadeInLeft">
                    <span className="urbanist-500">The things I love...</span>
                    <h2 className="urbanist-italics-500">Areas of Expertise</h2>
                </div>
            </div>
            <div className="row row-pt-md">
                <div className="col-md-4 text-center animate-box">
                    <div className="services expertise-1">
                    <span className="icon">
                        <link rel="stylesheet" href="https://cdn.jsdelivr.net/gh/devicons/devicon@v2.14.0/devicon.min.css"></link>
                        <i class="devicon-cplusplus-plain"></i>
                    </span>
                    <div className="desc">
                        <h3>GENERAL DEVELOPMENT</h3>
                        <p>Experience with data structures and algorithm - Applied knowledge in TPC/UDP packet processing, QTCreator and graphing applications, and driver communication applications</p>
                    </div>
                    </div>
                </div>
                <div className="col-md-4 text-center animate-box">
                    <div className="services expertise-2">
                    <span className="icon">
                        <link rel="stylesheet" href="https://cdn.jsdelivr.net/gh/devicons/devicon@v2.14.0/devicon.min.css"></link>
                        <i class="devicon-react-original"></i>
                    </span>
                    <div className="desc">
                        <h3>WEB DEVELOPMENT</h3>
                        <p>Developed custom websites for commercial and personal purposes (like this one!). Experience with Leaflet and React-Redux.</p>
                    </div>
                    </div>
                </div>
                <div className="col-md-4 text-center animate-box">
                    <div className="services expertise-3">
                    <span className="icon">
                    <link href="https://fonts.googleapis.com/icon?family=Material+Icons" rel="stylesheet"></link>
                        <i class="material-icons">&#xe308;</i>
                    </span>
                    <div className="desc">
                        <h3>TERAHERTZ COMMUNICATIONS</h3>
                        <p>Hands-on experience of what goes on in the hardware-level (I/Q Channels, IQ Constellations, QPSK) as well as in software-side (both driver and user space). </p>
                    </div>
                    </div>
                </div>
            </div>  
            <div className="row row-pt-md">  
                <div className="col-md-4 text-center animate-box">
                    <div className="services expertise-4">
                    <span className="icon">
                        <link rel="stylesheet" href="https://cdn.jsdelivr.net/gh/devicons/devicon@v2.14.0/devicon.min.css"></link>
                        <i class="devicon-c-plain"></i>
                    </span>
                    <div className="desc">
                        <h3>DRIVER CODE</h3>
                        <p>Experience with working with driver code for both AXI STREAM and DMA used to integrate into a RFSoC Dashboard. Ongoing projects involve working with PCIE driver code to make a complete over-the-air system.</p>
                    </div>
                    </div>
                </div>
                <div className="col-md-4 text-center animate-box">
                    <div className="services expertise-5">
                    <span className="icon">
                        <link href="https://fonts.googleapis.com/icon?family=Material+Icons" rel="stylesheet"></link>
                        <i class="material-icons">&#xe322;</i>
                    </span>
                    <div className="desc">
                        <h3>HARDWARE RTL DESIGN</h3>
                        <p>Incorporating RTL designs in PCIE and Dashboard projects - Used Vivado to work with ZynqMP projects with PCIE, AXI, and DMA modules.</p>
                    </div>
                    </div>
                </div>
                <div className="col-md-4 text-center animate-box">
                    <div className="services expertise-6">
                    <span className="icon">
                        <link href="https://fonts.googleapis.com/icon?family=Material+Icons" rel="stylesheet"></link>
                        <i class="material-icons">&#xe0b7;</i>
                    </span>
                    <div className="desc">
                        <h3>TIME MANAGEMENT</h3>
                        <p>By working on multiple simultaneous projects, tasks, and jobs, I have become all-to familiar with the importance of managing time, and energy to produce the highest quality work.</p>
                    </div>
                    </div>
                </div>
            
            </div>
        </div>
        </section>
      </div>
    )
  }
}
